.create-form
  display: flex
  flex-direction: column
  max-width: 702px
  width: 100%
  gap: 35px
  &__label
    @include poppins-medium
    display: grid
    font-size: 20px
    &-description
      @include poppins-regular
      grid-row: 2
      color: $input-text
      font-size: 13px
    &--checkbox
      display: grid
      grid-template-rows: repeat(2, 1fr)
      grid-template-columns: auto 55px
      align-items: center
      & input
        grid-column: 2
        grid-row: 1/3
    &--price
      grid-template-columns: 1fr 3fr
      @media (max-width: $mobile-only)
        grid-template-columns: repeat(2, 1fr)

  &__inner-wrapper
    display: grid
    max-width: 100%
    grid-template-columns: repeat(2, calc(50% - 35px / 2))
    gap: 35px
    @media (max-width: $mobile)
      gap: unset
      justify-content: space-between

  &__input
    @include input
    @include poppins-regular
    padding: 20px 25px
    font-size: 13px
    -webkit-appearance: none
    background-image: url("../../img/icons/input-pencil.svg")
    background-position: calc(100% - 20px) 50%
    background-repeat: no-repeat
    background-size: 20px 20px
    @media (max-width: $mobile)
      font-size: 9px
      padding: 15px 17px
    &::-webkit-inner-spin-button
      display: none
    &::placeholder
      color: $input-text
    &--textarea
      background-position: calc(100% - 20px) 25px
      resize: none
    &--select-currency
      padding: 15px 10px
      grid-column: 1
      border-top-right-radius: 0
      border-bottom-right-radius: 0
      background-image: url("../../img/icons/select-arrow.svg")
      background-size: 11px 6px
      background-position: calc(100% - 6px) 50%
    &--currency
      position: relative
      border-top-left-radius: 0
      border-bottom-left-radius: 0
      @media (max-width: $mobile)
        max-width: 120px
        padding: 15px 10px
      &::before
        position: absolute
        bottom: 10px
        left: 0
        display: block
        width: 1px
        height: 40px
        background-color: $pseudo-element-gray
        content: ''
    &--right
      @media (max-width: $mobile)
        max-width: 160px
    &--select
      background-image: url("../../img/icons/select-arrow.svg")
      background-size: 11px 6px
    &:not(:checked) + .create-form__checkbox
      background-color: #e7e4e4
      &::after
        right: 30px
        background-color: $black
    &:focus-visible
      outline: 2px solid $accent-dark
      background-blend-mode: difference

  &__checkbox
    position: relative
    display: block
    width: 55px
    height: 30px
    padding: 0
    border-radius: 20px
    background-color: $black
    background-image: none
    grid-row: 1 / 3
    transition: 0.3s
    &::after
      position: absolute
      top: 5px
      right: 5px
      display: block
      width: 20px
      height: 20px
      border-radius: 50%
      background-color: $white
      transition: 0.3s
      content: ''

  &__error-message
    position: fixed
    top: 50%
    left: 50%
    display: flex
    padding: 20px
    flex-direction: column
    justify-content: center
    gap: 20px
    border-radius: 20px
    box-shadow: 0px 20px 80px 0px rgba($black, 20%)
    transform: translate(-50%, -50%)
    background-color: $white
    z-index: 10
    & p
      @include poppins-regular
      text-align: center
